<template>
	<div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="0">
		<section>
			<swiper :options="swiperOption">
				<swiper-slide v-for="(slide, index) in swiperSlides" :key="index">
					<img :src="slide.img" alt="" class="swiperImg">
				</swiper-slide>
				<div class="swiper-pagination" slot="pagination"></div>
			</swiper>
		</section>
		<section class="nav">
			<router-link to="#">
				<figure>
					<!--28 -->
					<img src="static/img/learnCom.svg" alt="">
					<figcaption></figcaption>
				</figure>
			</router-link>
			<router-link to="#">
				<figure>
					<img src="static/img/lecture.svg" alt="">
					<figcaption></figcaption>
				</figure>
			</router-link>
			<router-link to="#">
				<figure>
					<img src="static/img/improve.svg" alt="">
					<figcaption></figcaption>
				</figure>
			</router-link>
		</section>
		<!-- <section> -->
			<!-- <header></header> -->
			<!--  -->
		<!-- </section> -->
		<section class="exercise">
			<header class="get_more">
				
				<router-link to="#">
					<img src="static/img/right.svg" alt="">
				</router-link>
			</header>
			<img :src="exerciseData[0].img" alt="" class="width_vw">
			<img :src="exerciseData[1].img" alt="" class="width_vw">
		</section>
		<section>
			<header class="get_more">
				
				<router-link to="#">
					<img src="static/img/right.svg" alt="">
				</router-link>
			</header>
			<ul>
				<li v-for="(item, index) in dataList" class="classInfo" :key="index">
					<div>
						<img :src="item.header_img" alt="" >
					</div>
					<div>
						<header class="row_ellipsis">{{item.title}}</header>
						<p class="row_ellipsis">{{item.describe}}</p>
						<footer>
							<span>
								{{item.subscribe}}
							</span>
							<span>
								{{item.price}}
							</span>
							<span>
								
							</span>
						</footer>
					</div>
				</li>
			</ul>
		</section>
	</div>
</template>
<script>
	import api from "../../fetch/date"
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
	export default{
		name: 'dateClassroom',
		data(){
			return {
				finish: false,
				busy: false,
				pageIndex: 0,
				dataList: [],
				exerciseData: ["",""],
				swiperSlides: [],
				swiperOption: {
					pagination: '.swiper-pagination',
					paginationClickable: true,
					spaceBetween: 30,
					centeredSlides: true,
					autoplay: 2500,
					loop : true,
					autoplayDisableOnInteraction: false,
					observer:true,//swiperswiper
    				observeParents:true,//swiperswiper
    			}
			}
		},
		created() {
			
		},
		mounted() {
			this.initData()			
		},
		components: {
			swiper, swiperSlide
		},
		computed: {
			
		},
		methods: {
			initData(){
				setTimeout(() => {
					this.getAds("CLASS_AD");
				},300);
				setTimeout(() => {
					this.getAds("TRAIN_AD");
				},300);
			},
			getAds(type){
				let data = {
					type: type
				}
				api.getAds(data)
					.then( res => {
						if(type == "CLASS_AD"){
							this.swiperSlides = res.message;
						}else if(type == "TRAIN_AD"){
							this.exerciseData = res.message;
						}
					})
					.catch( err => {
						console.log(err)
					})
			},

			getClass(){
				let data = {
					pageIndex: this.pageIndex,
					pageSize: 10,
				}
				api.getDateClass(data)
					.then(res => {
						if (res.message.length == 0) {
							this.finish = true;
						}
						this.dataList.push(...res.message);
						this.busy = false;
					})
					.catch( err => {
						console.log(err)
					})
			},

			loadMore(){
				if (!this.finish) {
					this.pageIndex += 1;
					this.busy = true;
					this.getClass();
				}else{
					return;
				}
			},
		}
	}
</script>
<style lang="scss" scoped>
.swiperImg {
	width: 100vw;
	height: 30vh;
}
.exercise {
	border: {
		top: .15rem solid #DCDCDC;
		bottom: .15rem solid #DCDCDC;
	}
	margin: {
		top: .2rem;
		bottom: .2rem;
	}
	padding: {
		top: .1rem;
		bottom: .1rem;
	}
}
.classInfo {
	display: flex;
	padding: 5vw;
	border: {
		bottom: 1px solid #DCDCDC;
	}
	header {
		font-size: .18rem;
		font-weight: bold;
		width: 60vw;
	}
	p {
		width: 60vw;
		margin: {
			top: 1vh;
			bottom: 1vh;
		}
	}
	footer {
		span:nth-child(1) {
			margin-right:20px;
		}
		span:nth-child(2) {
		color: red;
		}
	}
	img {
		width: 30vw;
		margin-right: 10px;
	}
}
</style>


